import React from 'react'
import { getAction,postAction } from '@/axios';
import {Modal,message} from 'antd';
import { withRouter } from 'react-router-dom';

class Details extends React.Component{
    constructor (props) {
        super(props);
        this.state = {
            record:{}
            // taskName:'名师计划申报表-表单创建',
            // startTime:'2019-04-11 24:00',
            // endTime:'2019-04-11 24:00',
            // taskType:"1",//任务类型
            // taskId:"0091"//任务id
        }
    }
    componentDidMount(){
        this.getMsgDetail();
    }
    //获取消息详情
    getMsgDetail=()=>{
        getAction(`ybt/message/getMessage?id=${this.props.currentMsgId}`).then(res => {
            if(res.success){
                // console.log(res.obj);
                this.setState({
                    record:res.obj||{}
                })
            }else{
                message.error(res.obj)
            }
        })
    }
    jumpTo = () => {
        //判断当前点击的taskType类型
        // 2，创建表单审核，1，创建计划审核，4，填报计划审核，3，填报任务，5，系统通知
        if(this.state.record.templateType===3){//填报任务
            getAction(`/ybt/form/task/info`, {//如果超时则不跳转
                formTaskId: this.state.record.businessId
            }).then(res=>{
                if(res.success){
                    if(res.obj && res.obj.taskStatus===5){
                        message.info("该计划已超时,不可填报");
                        return false;
                    }else{
                        this.props.history.push(`/myFillingTask/step2?id=${this.state.record.businessId}&type=fill`)
                    }
                }else{
                    message.error(res.obj)
                }
            })
        }else if(this.state.record.templateType===5){//系统通知
            console.log("系统通知，无跳转")
        }else{//审核跳转
            postAction("/ybt/wf/approveTask/getApproveNodeTaskById",{processNodeTaskId:this.state.record.businessId}).then(res=>{
                console.log(res);
                if(res.success){
                    if(!res.obj.taskEndTime){
                        this.props.history.push(`/auditManagement/audit?id=${this.state.record.businessId}`)
                    }else{
                        message.warning("当前审批阶段已结束！")
                    }
                }else{
                    message.error(res.obj)
                }
            })
        }
    }
    render(){
        let {record} = this.state;
        let time=null
        if(record.messageTemplateId === '1' || record.messageTemplateId === '4' || record.messageTemplateId === '2'){
            time=<React.Fragment>
                <p><span>开始时间：</span><span>{record.taskPlanStartTime}</span></p>
                <p><span>结束时间：</span><span>{record.taskPlanEndTime}</span></p> 
            </React.Fragment>
        }else if(record.messageTemplateId === '3' || record.messageTemplateId === '9'){
            if(record.taskType === '1'){
                time=<React.Fragment>
                <p><span>开始时间：</span><span>{record.taskPlanStartTime}</span></p>
                <p><span>结束时间：</span><span>{record.taskPlanEndTime}</span></p> 
                </React.Fragment>
            }else{
                time=<React.Fragment>
                <p><span>发起时间：</span><span>{record.taskStartTime}</span></p>
                </React.Fragment>
            }
        }
        let link=null
        if(record.urlFlag === 1){
            // 有链接
            if(record.templateType !== 5){
                //系统通知没有跳转功能
                link=<span style={{color:"#4164E4", cursor:"pointer", textDecoration:'underline'}} onClick={this.jumpTo}>点击此处跳转至处理页面</span>
            }
        }
        return (
            <Modal
                title="消息详情"
                visible={true}
                onCancel={this.props.hideDetails}
                footer = {null}
                bodyStyle={{padding: "28px 50px", lineHeight: "30px"}}
            >
                <p>{record.messageContent}</p>
                <p><span>任务名称：</span><span>{record.messageTitle}</span></p>
                {time}
                {link}
            </Modal>
        )
    }
}

export default withRouter(Details)