import React from 'react'
import {Form,Input, Button, Select} from 'antd'
import './Filter.css'
const { Option } = Select;

class Filter extends React.Component{
    constructor(props){
        super(props);
        this.state={
            messageContent:'', // 标题名称
            sendUserName:'', // 发布人
            templateType: '', // 消息类型
            messageStatus:'' // 状态
        }
    }
    handleSubmit=e=>{
        e.preventDefault();
        // console.log(this.props.form.getFieldValue());
        this.props.form.validateFieldsAndScroll((err, values) => {
            if (!err) {
              console.log('表单值: ', values);
              this.props.filtersChange(values);
              //清空表单的值
            //   this.props.form.resetFields();
            }
        });
    }
    handleReset=()=>{
        this.props.form.resetFields()
        this.props.filtersChange({
            messageContent:'', // 标题名称
            sendUserName:'', // 发布人
            templateType: '', // 消息类型
            messageStatus:'' // 状态
        }, 'reset')
    }
    render () {
        const { getFieldDecorator } = this.props.form;
        const { messageContent, sendUserName, templateType, messageStatus} = this.state;
        return (
            <div className="news-center-filter">
                <Form layout="inline" onSubmit={this.handleSubmit} >
                    <Form.Item label="消息内容：">
                        {getFieldDecorator('messageContent', {
                            initialValue:messageContent
                        })(
                        <Input placeholder="请输入消息内容" />
                        )}
                    </Form.Item>
                    <Form.Item label="发布人：">
                        {getFieldDecorator('sendUserName', {
                            initialValue:sendUserName
                        })(
                        <Input placeholder="请输入发布人" />
                        )}
                    </Form.Item>
                    <Form.Item label="消息类型：">
                        {getFieldDecorator('templateType', {
                            initialValue:templateType
                        })(
                            <Select style={{ width: 200 }}>
                                <Option value="">全部</Option>
                                <Option value="1">审核任务消息</Option>
                                <Option value="2">填报任务消息</Option>
                                <Option value="3">系统通知</Option>
                            </Select>,
                        )}
                    </Form.Item>
                    <Form.Item label="状态：">
                        {getFieldDecorator('messageStatus', {
                            initialValue:messageStatus
                        })(
                            <Select style={{ width: 200 }}>
                                <Option value="">全部</Option>
                                <Option value="0">未读</Option>
                                <Option value="1">已读</Option>
                            </Select>,
                        )}
                    </Form.Item>
                    <Form.Item>
                        <Button ghost type="primary" icon="search" htmlType="submit"  style={{marginRight:12}}>
                            搜索
                        </Button>
                        <Button icon="sync" onClick={this.handleReset}>重置</Button>
                    </Form.Item>
                </Form> 
            </div>
        )
    }
}

const WrappedFilter = Form.create({ name: 'register' })(Filter);
export default WrappedFilter;