import React from 'react'
import {Table, Button} from 'antd'
import Details from './Details'
import { postAction } from '@/axios';
import { message } from 'antd';
// const newsTypeList = [
//     {label:"通知消息",value:"1"},
//     {label:"系统消息",value:"2"},
// ]

class NewsTable extends React.Component{
    constructor(props) {
        super(props)
        this.state={
            columns: [
                {
                    title: '序号',
                    dataIndex: 'sortIndex',
                    width:'5%'
                },
                {
                    title: '消息内容',
                    dataIndex: 'messageContent',
                    width:'30%',
                    render:(text,record)=>{
                        return <span title={text}>{text}</span>
                    }
                },
                {
                    title: '消息类型',
                    dataIndex: 'messageType',
                },
                {
                    title: '发布人',
                    dataIndex: 'name',
                },
                {
                    title: '发布时间',
                    dataIndex: 'createTime',
                },
                {
                    title: '状态',
                    dataIndex: 'readStatus',
                    render: (text) => (text==="已读" ? '已读' : '未读')
                },
                {
                    title: '操作',
                    dataIndex: 'handle',
                    render: (text, record) => (<span style={{color:'#1890ff',textDecoration:'underline', cursor:'pointer'}} onClick={this.showDetails.bind(this, record.messageId)}>查看</span>)
                }
            ],
            dataSource:[ ],
            pagination:{
                current: 1,
                pageSize: 10,
                total:0,
                showTotal: (total) => (`共${total}条数据`),
                showQuickJumper:true,
                // showSizeChanger:true,
            },
            detailsStatus: false,
            currentMsgId:null,
            messageIdArr:[],//选中的消息id
            selectedRowKeys:[]//选中行的key
        }
    }
    componentDidMount(){
        this.getMessageList(this.props);
        let messageId = window.location.href.split('id=')[1];
        if(messageId){
            this.showDetails(messageId);
        }
        document.addEventListener("keydown",this._onkeydown);
    }
    _onkeydown = (e) => {
        if(e.keyCode === 13){
            this.getMessageList(this.props);
        }
    }
    componentWillUnmount(){
        document.removeEventListener("keydown",this._onkeydown)
    }
    componentDidUpdate(preProps){
        if(JSON.stringify(this.props)!==JSON.stringify(preProps)){
            if(preProps.isReset !== this.props.isReset){
                this.setState({
                    pagination: {
                        ...this.state.pagination,
                        current: 1
                    }
                }, () => {
                    this.getMessageList(this.props);
                })
                return false;
            }
            this.getMessageList(this.props);
        }
    } 
    //查询消息列表  
    getMessageList=(props,page)=>{
        let current = 1;
        if(page==="page"){
            current = this.state.pagination.current;
        }
        let data = props.filters;
        data.index = 0;//列表查询传0
        let url = `/ybt/message/getMessageList?pageNum=${current}&pageSize=${this.state.pagination.pageSize}`;
        postAction(url,data).then(res => {
            if(res.success){
                //设置分页数据
                let pagination = JSON.parse(JSON.stringify(this.state.pagination));
                pagination.total = res.obj.total;
                pagination.current = res.obj.page;
                pagination.pageSize = res.obj.pageSize;

                //设置表格数据
                let dataSource = res.obj.rows||[];
                dataSource.forEach((item,index)=>{
                     item.sortIndex = index+1;
                });
                // console.log(dataSource);
                this.setState({
                    dataSource,
                    pagination
                })
            }else{
                message.error(res.obj)
            }
        })
    }
    //分页、排序、筛选变化时触发
    handleTableChange = (pagination, filters, sorter) => {
        // console.log(pagination, filters, sorter);
        this.setState({
            pagination
        },()=>{
            this.getMessageList(this.props,"page");
        })
    }
    //查看信息
    showDetails = (messageId) => {
        // console.log(messageId);
        this.setState({
            currentMsgId: messageId,
            detailsStatus: true
        })
    }
    //关闭查看信息弹框
    hideDetails = () => {
        this.setState({
            detailsStatus: false
        })
    }
    //设为已读
    setMessageHaveRead=()=>{
        if(this.state.messageIdArr.length>0){
            postAction('/ybt/message/setMessageHaveRead',this.state.messageIdArr).then(res => {
                if(res.success){
                    message.success("设为已读成功");
                    this.getMessageList(this.props);
                    this.setState({
                        selectedRowKeys:[]
                    })
                }else{
                    message.error(res.obj)
                }
            })
        }else{
            message.warning("请勾选消息")
        }
    }
    render () {
        const rowSelection = {
            onChange: (selectedRowKeys, selectedRows) => {
                // console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
                let messageIdArr = selectedRows.length>0?selectedRows.map(item=>item.messageId):[];
                this.setState({
                    messageIdArr,
                    selectedRowKeys
                })
            },
            getCheckboxProps: record => ({
              disabled: record.name === 'Disabled User', // Column configuration not to be checked
              name: record.name,
            }),
            selectedRowKeys:this.state.selectedRowKeys//受控：选中的key
        };
        return (
            <div>
                {
                    this.state.detailsStatus
                    ?
                    <Details hideDetails = {this.hideDetails} currentMsgId = {this.state.currentMsgId} />
                    :
                    null
                }
                <Table
                    rowKey="sortIndex"
                    columns={this.state.columns}
                    dataSource={this.state.dataSource}
                    bordered
                    rowSelection={rowSelection}
                    pagination={this.state.pagination}
                    onChange={this.handleTableChange}
                    title={() => (<Button type='primary' onClick={this.setMessageHaveRead}>标记已读</Button>)}
                />
            </div>
        )
    }
}

export default NewsTable