import React from 'react'
import {Card} from 'antd'
import Filter from './Filter'
import NewsTable from './NewsTable'

class NewsCenter extends React.Component{
    constructor(props){
        super(props);
        this.state={
            filters: {
                messageTitle:'', // 标题名称
                sendUserName:'', // 发布人
                templateType: '', // 消息类型
                messageStatus:'' // 状态
            },
            isReset: 0,
        }
    }
    filtersChange = (filters, type) => {
        if(type === 'reset'){
            let {isReset} = this.state;
            isReset++;
            this.setState({isReset})
        }
        this.setState({
            filters
        })
    }
    render(){
        return (
            <Card title="全部消息" style={{margin:"20px"}}>
                <Filter filtersChange = {this.filtersChange} />
                <NewsTable filters = {this.state.filters} isReset={this.state.isReset}/>
            </Card>
        )
    }
}

export default NewsCenter